import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"

const Bio = () => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      site {
        siteMetadata {
          author {
            name
            portfolio
          }
        }
      }
    }
  `)

  const author = data.site.siteMetadata?.author

  return (
    <div className="bio">
      {author?.name && (
        <p>
          Written by me,
          {` `}
          <a href={`${author?.portfolio || ``}`}>
            <strong>{author.name}</strong>
          </a>
        </p>
      )}
    </div>
  )
}

export default Bio
